.catalog__list {
  @include reset-list;

  display: flex;
  justify-content: center;
  align-items: center;
  flex-wrap: wrap;
}

.catalog__item {
  margin-left: 1%;
  margin-right: 1%;
  margin-top: 1%;
  margin-bottom: 1%;
}

.catalog__link img {
  display: block;
}

.catalog__icon--heart {
  display: none;
}

.catalog__description {
  display: none;
}

@media (max-width: $tablet-width) {
  .catalog__item {
    display: flex;
    flex-direction: column;
    flex-basis: 48%;
    justify-content: center;
    align-items: center;
    margin-top: 2%;
    margin-bottom: 2%;
  }

  .catalog__item-inner {
    display: flex;
    flex-direction: column;
  }

  .catalog__link {
    position: relative;

    margin-bottom: 20px;
  }

  .catalog__icon--heart {
    position: absolute;
    bottom: 20px;
    left: 20px;

    display: block;
    width: 37px;
    height: 32px;

    fill: #ffffff;
  }

  .catalog__description {
    display: block;
  }

  .catalog__description h3 {
    margin: 0;
    margin-bottom: 5px;

    font-family: "Arial", "Verdana", sans-serif;
    font-style: normal;
    font-weight: normal;
    font-size: 16px;
    line-height: 28px;

    letter-spacing: 0.04em;
    text-transform: uppercase;

    color: #21201f;
  }

  .catalog__item-price {
    font-family: "Arial", "Verdana", sans-serif;
    font-style: normal;
    font-weight: 700;
    font-size: 16px;
    line-height: 28px;
    text-transform: uppercase;
    color: #21201f;
  }
}

@media (max-width: $mobile-width) {
  .catalog__item {
    flex-basis: auto;
    margin-left: 0;
    margin-right: 0;
  }

  .catalog__link {
    margin-bottom: 15px;
  }

  .catalog__icon--heart {
    bottom: 10px;
    left: 10px;
  }

  .catalog__description h3 {
    font-size: 14px;
    line-height: 18px;
  }

  .catalog__item-price {
    font-size: 14px;
    line-height: 18px;
  }
}
