.product-card__wrapper {
  display: flex;
}

.product-card__image-wrapper {
  display: flex;
}

.product-card__image {
  display: block;
}

.product-card__description-wrapper {
  margin-left: 10px;
  margin-right: 10px;
}

.product-card__description-wrapper h3 {
  margin: 0;
  margin-bottom: 20px;

  font-family: "Arial", "Verdana", sans-serif;
  font-style: normal;
  font-weight: normal;
  font-size: 20px;
  line-height: 32px;

  letter-spacing: 0.04em;
  text-transform: uppercase;

  color: #21201f;
}

.product-card__description-inner {
  margin-left: 20px;
  margin-right: 20px;
}

.product-card__price {
  display: block;
  margin-bottom: 20px;

  font-family: "Arial", "Verdana", sans-serif;
  font-style: normal;
  font-weight: 400;
  font-size: 16px;
  line-height: 20px;
  text-transform: uppercase;
  color: #21201f;
}

.product-card__color-wrapper {
  margin-bottom: 20px;
}

.product-card__color,
.product-card__size {
  display: block;
  margin-bottom: 10px;

  font-family: "Arial", "Verdana", sans-serif;
  font-style: normal;
  font-weight: 400;
  font-size: 16px;
  line-height: 20px;
  text-transform: uppercase;
  color: #8c8c8c;
}

.product-card__size-wrapper {
  margin-bottom: 20px;
}

.product-card__color-item {
  content: "";

  width: 34px;
  height: 34px;

  border: 1px solid #21201f;
  background-color: #dfded9;
}

.product-card__size-list {
  @include reset-list;
  display: flex;
}

.product-card__size-item {
  margin-left: 10px;
  margin-right: 10px;
}

.product-card__button-wrapper {
  display: flex;
  max-width: 235px;

  background-color: #21201f;
}

.product-card__cart-button {
  display: flex;
  align-items: center;
  justify-content: center;
  padding: 0;
  padding-top: 15px;
  padding-bottom: 15px;
  padding-left: 33px;
  padding-right: 33px;

  font-family: "Arial", "Verdana", sans-serif;
  font-style: normal;
  font-weight: 400;
  font-size: 18px;
  line-height: 22px;
  text-transform: uppercase;
  color: #ffffff;

  border: none;
  background-color: inherit;
}

.product-card__wishlist-button {
  display: flex;
  align-items: center;
  justify-content: center;
  padding: 0;
  padding-top: 15px;
  padding-bottom: 15px;
  padding-right: 13px;
  padding-left: 13px;

  border: none;
  border-left: 2px solid #ffffff;
  background-color: inherit;
}

.product-card__icon--heart {
  width: 30px;
  height: 26px;
  fill: #ffffff;
}

.product-card__description-list {
  @include reset-list;
}

.product-card__description-button {
  position: relative;

  display: flex;
  padding: 0;
  padding-left: 30px;
  margin-bottom: 20px;

  font-family: "Arial", "Verdana", sans-serif;
  font-style: normal;
  font-weight: 400;
  font-size: 18px;
  line-height: 22px;
  text-transform: uppercase;
  color: #21201f;

  background-color: transparent;
  border: none;

  &::before {
    content: "";

    position: absolute;
    top: 3px;
    left: 9px;

    width: 16px;
    height: 16px;

    background-image: url("../img/vector/icon-plus.svg");
    background-repeat: no-repeat;
  }
}
