.modal-login__wrapper {
  width: 565px;
  height: 645px;
  margin-left: auto;
  margin-right: auto;
  padding: 40px;
}

.modal-login__inner {
  display: flex;
  justify-content: space-between;
  align-items: baseline;
}

.modal-login__inner h2 {
  margin: 0;
  margin-bottom: 35px;

  font-family: "Arial", "Verdana", sans-serif;
  font-style: normal;
  font-weight: normal;
  font-size: 32px;
  line-height: 48px;
  text-transform: uppercase;
  letter-spacing: 0.04em;
  color: #21201f;
}

.modal-login__input-inner {
  display: flex;
  justify-content: space-between;
  align-items: center;
  min-height: 40px;
  margin-bottom: 30px;
}

.modal-login__input-inner input {
  padding: 0;
  padding-bottom: 5px;
  flex-grow: 1;

  border: none;
  border-bottom: 2px solid #d8d8d8;
  outline: none;
}

.modal-login__close {
  display: flex;
  align-items: center;
  justify-content: center;
  padding: 0;

  background-color: transparent;
  border: none;
}

.modal-login__checkbox-inner {
  position: relative;
  min-height: 32px;
  margin-bottom: 20px;
}

.modal-login__checkbox-label::before {
  content: "";

  position: absolute;
  left: 0;

  width: 32px;
  height: 32px;

  background-color: #21201f;
}

.modal-login__checkbox:checked + .modal-login__checkbox-label::after {
  content: "";

  position: absolute;

  width: 32px;
  height: 32px;

  background-image: url("../../img/vector/icon-checkbox-on.svg");
  background-position: center;
  background-repeat: no-repeat;
}

.modal-login__subscribe {
  margin: 0;
  padding-left: 40px;
}

.modal-login__rules {
  margin-bottom: 30px;

  color: #8c8c8c;
}

.modal-login__link {
  text-decoration: underline;
  color: #21201f;
}

.modal-login__signup {
  display: flex;
  align-items: center;
  justify-content: center;
  padding: 0;
  padding-left: 80px;
  padding-right: 80px;
  padding-top: 15px;
  padding-bottom: 15px;
  margin-left: auto;
  margin-right: auto;
  margin-bottom: 30px;

  font-family: "Arial", "Verdana", sans-serif;
  font-style: normal;
  font-weight: normal;
  font-size: 20px;
  line-height: 24px;
  text-transform: uppercase;
  letter-spacing: 0.04em;
  color: #ffffff;

  border: none;
  background-color: #21201f;

  cursor: pointer;
}

.modal-login__signup-link {
  font-family: "Arial", "Verdana", sans-serif;
  font-style: normal;
  font-weight: normal;
  font-size: 18px;
  line-height: 22px;
  text-transform: uppercase;
  text-decoration: underline;
  letter-spacing: 0.04em;
  color: #21201f;
}
