.page-header__wrapper {
  display: flex;
  flex-direction: column;
  padding-left: 20px;
  padding-right: 20px;
  padding-top: 20px;
  padding-bottom: 20px;
}

.page-header__nav-wrapper {
  display: flex;
  align-items: center;
  justify-content: center;
}

.page-header__toggle {
  display: none;
}

.page-header__nav {
  flex: 1;
}

.page-header__list {
  @include reset-list;

  display: flex;

  &--user-nav {
    flex: 1;
    align-items: center;
    justify-content: flex-end;
  }
}

.page-header__item {
  padding-left: 14px;
  padding-right: 14px;
}

.page-header__link {
  display: block;

  font-family: "Arial", "Verdana", sans-serif;
  font-style: normal;
  font-weight: normal;
  font-size: 14px;
  line-height: 18px;
  text-transform: uppercase;

  color: #21201f;
}

.page-header__logo {
  flex: 1;
  text-align: center;
}

.page-header__icon {
  vertical-align: middle;

  fill: #21201f;
}

.page-header__logo--image {
  width: 305px;
  height: 30px;
}

.page-header__button {
  display: flex;
  align-items: center;
  justify-content: center;
  padding: 0;

  font-family: "Arial", "Verdana", sans-serif;
  font-style: normal;
  font-weight: normal;
  font-size: 14px;
  line-height: 14px;
  text-transform: uppercase;

  color: #21201f;

  background-color: transparent;
  border: none;

  cursor: pointer;

  &:hover {
    opacity: 0.7;
  }

  &:focus,
  &:active {
    opacity: 0.3;
  }
}

.page-header__icon--search {
  width: 16px;
  height: 16px;
}

.page-header__form {
  display: none;
  justify-content: space-between;
  align-items: center;
  margin-left: 12px;

  border-bottom: 1px solid white;
}

.page-header__form input {
  width: 100%;

  font-family: "Arial", "Verdana", sans-serif;
  font-style: normal;
  font-weight: normal;
  font-size: 16px;
  line-height: 32px;
  color: #ffffff;

  border: none;

  background-color: transparent;

  &::placeholder {
    color: #9b9997;
  }
}

.page-header__form label {
  width: 100%;
}

.page-header__search-text {
  display: inline-block;
  padding-left: 5px;

  font-family: "Arial", "Verdana", sans-serif;
  font-style: normal;
  font-weight: normal;
  font-size: 14px;
  line-height: 14px;
  text-transform: uppercase;

  color: #21201f;
}

.page-header__icon--close {
  width: 20px;
  height: 20px;
}

.page-header__icon--cart {
  display: none;
}

.page-header__icon--heart {
  width: 24px;
  height: 20px;
}

.page-header--active {
  background-color: #21201f;

  .page-header__nav-wrapper {
    margin-bottom: 20px;
  }

  .page-header__form {
    display: flex;
  }

  .page-header__button--close {
    display: flex;
  }
}

.page-header__list--subnav {
  display: none;
}

.page-header__button--close-menu {
  display: none;
}

@media (max-width: $tablet-width) {
  .page-header {
    position: sticky;
    top: 0;
    left: 0;
    right: 0;
    z-index: 2;
  }

  .page-header__wrapper {
    background-color: #ffffff;
  }

  .page-header__toggle {
    display: flex;
    align-items: center;
    justify-content: flex-start;
    padding: 0;

    border: none;

    background-color: transparent;
  }

  .page-header__list--main-nav {
    display: none;

    font-family: "Arial", "Verdana", sans-serif;
    font-style: normal;
    font-weight: 400;
    font-size: 20px;
    line-height: 32px;
    letter-spacing: 0.04em;
    text-transform: uppercase;
  }

  .page-header__mobile-menu--active {
    display: block;
    background-color: #21201f;
    position: fixed;
    top: 0;
    left: 0;
    z-index: 100;
    overflow: auto;
    padding: 30px;
    width: 50%;
    height: 100%;
    transition: left 0.1s linear;

    .page-header__list--main-nav {
      display: flex;
    }

    .page-header__list--subnav {
      display: flex;
    }

    .page-header__button--close-menu {
      display: block;
      position: absolute;

      top: 32px;
      right: 30px;
    }
  }

  .page-header__icon--menu {
    width: 40px;
    height: 28px;
  }

  .page-header__item {
    padding-left: 20px;
    padding-right: 20px;
  }

  .page-header__icon--logo {
    width: 285px;
  }

  .page-header__list--user-nav {
    .page-header__item:last-child {
      padding-right: 0;
    }
  }

  .page-header__icon--search {
    width: 32px;
    height: 32px;
  }

  .page-header__item--login {
    display: none;
  }

  .page-header__search-text {
    display: none;
  }

  .page-header__bag {
    display: none;
  }

  .page-header__icon--cart {
    display: block;
  }

  .page-header__icon--heart {
    width: 36px;
    height: 32px;
  }

  .page-header__icon--close {
    width: 23px;
    height: 23px;
  }

  .page-header--active {
    .page-header__form {
      margin-left: 0;
    }
  }
}

@media (max-width: $mobile-width) {
  .page-header__wrapper {
    padding-left: 2%;
    padding-right: 2%;
  }

  .page-header__item {
    padding-left: 10px;
    padding-right: 10px;
  }

  .page-header--index-page {
    position: absolute;

    .page-header__wrapper {
      background-color: transparent;
    }

    .page-header__link {
      color: #ffffff;
    }

    .page-header__icon {
      fill: #ffffff;
    }

    .page-header__search-text {
      color: #ffffff;
    }

    .page-header__button {
      color: #ffffff;
    }
  }

  .page-header__icon--logo {
    width: 150px;
  }

  .page-header__icon--menu {
    width: 28px;
    height: 20px;
  }

  .page-header__icon--search {
    width: 25px;
    height: 25px;
  }

  .page-header__icon--cart {
    width: 21px;
    height: 25px;
  }

  .page-header__icon--heart {
    width: 28px;
    height: 25px;
  }

  .page-header__form input {
    font-size: 14px;
    line-height: 28px;
  }

  .page-header__mobile-menu--active {
    width: 80%;
    padding: 15px;

    .page-header__button--close-menu {
      top: 28px;
      right: 20px;
    }
  }
}
