.slider__wrapper {
  padding-left: 4%;
  padding-right: 4%;
  padding-bottom: 60px;
}

.slider__list {
  @include reset-list;
}

.slider__item {
  background-position: 0 0;
  background-size: 100% auto;
  background-repeat: no-repeat;
  // position: absolute;
  // top: 0;
  // left: 0;
  // width: 100%;
  // height: 100%;
  overflow: hidden;
}

.slider__inner h3 {
  margin: 0;
  margin-bottom: 10px;

  font-family: $syncopate-font;
  font-style: normal;
  font-weight: 700;
  font-size: 40px;
  line-height: 40px;
  text-transform: uppercase;

  color: #ffffff;
}

.slider__inner p {
  margin: 0;
  margin-bottom: 20px;

  font-family: "Arial", "Verdana", sans-serif;
  font-style: normal;
  font-weight: normal;
  font-size: 20px;
  line-height: 22px;

  letter-spacing: 0.1em;
  color: #ffffff;
}

.slider__link {
  display: flex;
  justify-content: center;
  align-items: center;
  max-width: 330px;
  padding-top: 20px;
  padding-bottom: 20px;
  padding-left: 40px;
  padding-right: 40px;

  font-family: "Arial", "Verdana", sans-serif;
  font-style: normal;
  font-weight: normal;
  font-size: 22px;
  line-height: 22px;
  text-align: center;
  text-transform: uppercase;
  color: #ffffff;

  border: 2px solid #ffffff;
}

.slider__inner {
  padding-left: 10%;
  max-width: 700px;
  padding-top: 27%;
  padding-bottom: 7%;
}

.slider__item--tattoos-girl {
  background-image: url("../img/slider-girl-with-tattoos-desktop@1x.jpg");
}

.slider__item--three-girls {
  background-image: url("../img/slider-three-girls-desktop@1x.jpg");
}

.slider__item--girl-and-sky {
  background-image: url("../img/slider-girl-and-sky-desktop@1x.jpg");
}

@media (max-width: $tablet-width) {
  .slider {
    position: relative;
  }

  .slider__wrapper {
    padding-left: 100px;
    padding-right: 100px;
    padding-bottom: 30px;
  }

  .slider__inner {
    display: flex;
    justify-content: center;
    flex-direction: column;
    max-width: none;
    padding-left: 0;
    padding-right: 0;
    padding-top: 100%;
    padding-bottom: 0;
  }

  .slider__inner h3 {
    font-size: 64px;
    line-height: 70px;
    text-align: center;
    color: #21201f;
  }

  .slider__inner p {
    font-size: 24px;
    line-height: 30px;
    text-align: center;
    color: #21201f;
  }

  .slider__item--tattoos-girl {
    background-image: url("../img/slider-girl-with-tattoos-tablet@1x.jpg");
  }

  .slider__item--three-girls {
    background-image: url("../img/slider-three-girls-tablet@1x.jpg");
  }

  .slider__item--girl-and-sky {
    background-image: url("../img/slider-girl-and-sky-tablet@1x.jpg");
  }

  .slider__link {
    padding-top: 25px;
    padding-bottom: 25px;
    margin-left: auto;
    margin-right: auto;
    min-width: 400px;

    font-size: 28px;
    line-height: 28px;
    text-align: center;
    color: #21201f;

    border: 2px solid #21201f;
  }
}

@media (max-width: $mobile-width) {
  .slider__wrapper {
    padding-left: 0;
    padding-right: 0;
    padding-bottom: 20px;
  }

  .slider__inner h3 {
    margin-left: auto;
    margin-right: auto;
    max-width: 320px;

    font-size: 36px;
    line-height: 42px;
  }

  .slider__inner p {
    font-size: 16px;
  }

  .slider__link {
    min-width: 300px;
    padding-top: 15px;
    padding-bottom: 15px;

    font-size: 18px;
    line-height: 18px;
  }
}
