.filter {
  min-width: 140px;
}

.filter__button {
  display: none;
}

.filter__inner h3 {
  margin: 0;
  margin-bottom: 25px;

  font-family: "Arial", "Verdana", sans-serif;
  font-style: normal;
  font-weight: normal;
  font-size: 20px;
  line-height: 32px;

  letter-spacing: 0.04em;
  text-transform: uppercase;

  color: #21201f;
}

.filter__inner legend {
  //временно
  margin-bottom: 25px;

  font-family: "Arial", "Verdana", sans-serif;
  font-style: normal;
  font-weight: 400;
  font-size: 14px;
  line-height: 18px;
  text-transform: uppercase;
  color: #21201f;
}

@media (max-width: $tablet-width) {
  .filter__wrapper {
    display: none;
  }

  .filter__button-wrapper {
    margin-left: 30px;
    margin-right: 30px;

    border-bottom: 1px dashed #d8d8d8;
  }

  .filter__button {
    position: relative;

    display: flex;
    padding: 0;
    padding-left: 30px;
    margin-bottom: 20px;

    font-family: "Arial", "Verdana", sans-serif;
    font-style: normal;
    font-weight: normal;
    font-size: 20px;
    line-height: 32px;

    letter-spacing: 0.04em;
    text-transform: uppercase;

    background-color: transparent;
    border: none;

    &::before {
      content: "";

      position: absolute;
      top: 8px;
      left: 9px;

      width: 16px;
      height: 16px;

      background-image: url("../img/vector/icon-plus.svg");
      background-repeat: no-repeat;
    }
  }
}

@media (max-width: $mobile-width) {
  .filter__button-wrapper {
    margin-left: 0;
    margin-right: 0;
  }

  .filter__button {
    margin-bottom: 10px;
  }
}
