.page-main--catalog {
  display: flex;
  padding-top: 30px;
  padding-bottom: 60px;
  padding-right: 4%;
  padding-left: 4%;
}

@media (max-width: $tablet-width) {
  .page-main--catalog {
    display: block;
    padding-bottom: 30px;
    padding-right: 20px;
    padding-left: 20px;
  }
}

@media (max-width: $mobile-width) {
  .page-main--catalog {
    padding-top: 20px;
    padding-bottom: 20px;
    padding-left: 2%;
    padding-right: 2%;
  }
}
