@media (max-width: $tablet-width) {
  .subnav {
    @include reset-list;

    flex-direction: column;
  }

  .subnav__item {
    padding: 0;
    margin-top: 10px;
    margin-bottom: 10px;
  }

  .subnav__link {
    font-family: "Arial", "Verdana", sans-serif;
    font-style: normal;
    font-weight: 400;
    font-size: 20px;
    line-height: 32px;
    letter-spacing: 0.04em;
    text-transform: uppercase;
  }
}
