@font-face {
  font-family: "Syncopate";
  font-style: normal;
  font-weight: 400;
  src: url("../fonts/syncopate-v12-latin-regular.woff2") format("woff2"),
    url("../fonts/syncopate-v12-latin-regular.woff") format("woff");
}

@font-face {
  font-family: "Syncopate";
  font-style: normal;
  font-weight: 700;
  src: url("../fonts/syncopate-v12-latin-700.woff2") format("woff2"),
    url("../fonts/syncopate-v12-latin-700.woff") format("woff");
}

*,
*::before,
*::after {
  box-sizing: border-box;
}

body {
  margin: 0;
  padding: 0;

  font-family: "Arial", "Verdana", sans-serif;
  font-style: normal;
  font-weight: normal;
  font-size: 14px;
  line-height: 14px;
  color: #21201f;

  background-color: #ffffff;
}

img {
  max-width: 100%;
  height: auto;
}

a {
  text-decoration: none;
  cursor: pointer;
}

.visually-hidden,
.input[type="checkbox"].visually-hidden,
.input[type="radio"].visually-hidden {
  position: absolute;

  width: 1px;
  height: 1px;
  margin: -1px;
  border: 0;
  padding: 0;

  white-space: nowrap;

  clip-path: inset(100%);
  clip: rect(0 0 0 0);
  overflow: hidden;
}
