.subscribe h2 {
  margin: 0;
  margin-bottom: 15px;

  font-style: normal;
  font-weight: normal;
  font-size: 20px;
  line-height: 32px;

  text-align: center;
  letter-spacing: 0.04em;
  text-transform: uppercase;

  color: #21201f;
}

.subscribe p {
  margin: 0;
  margin-bottom: 40px;

  font-style: normal;
  font-weight: normal;
  font-size: 14px;
  line-height: 18px;

  text-align: center;

  color: #21201f;
}

.subscribe__inner {
  display: flex;
  justify-content: space-between;
  align-items: center;
  min-height: 40px;

  border-bottom: 1px solid #d8d8d8;
}

.subscribe input {
  padding: 0;
  flex-grow: 1;

  font-size: 16px;
  line-height: 32px;
  color: #21201f;
  letter-spacing: 0.04em;

  border: none;
  background-color: transparent;

  &::placeholder {
    color: #d8d8d8;
  }

  &:hover {
    opacity: 0.7;
  }

  &:focus,
  &:active {
    opacity: 1;
  }
}

.subscribe button {
  padding: 0;

  font-size: 20px;
  line-height: 32px;
  color: #21201f;
  letter-spacing: 0.04em;
  text-transform: uppercase;

  border: none;
  background-color: transparent;
  cursor: pointer;

  &:hover {
    opacity: 0.7;
  }

  &:focus,
  &:active {
    opacity: 0.3;
  }
}

@media (max-width: $mobile-width) {
  .subscribe input {
    font-size: 14px;
    line-height: 28px;
  }

  .subscribe p {
    margin-bottom: 20px;
  }
}
