.gallery__wrapper {
  padding-top: 60px;
  padding-bottom: 60px;
}

.gallery__inner {
  display: flex;
  justify-content: center;
  margin-bottom: 30px;
}

.gallery__inner img {
  display: block;
}

.gallery__title-wrapper {
  display: flex;
  margin-bottom: 30px;
}

.gallery__title-wrapper h2 {
  margin: 0;
  margin-right: 60px;

  font-family: "Arial", "Verdana", sans-serif;
  font-style: normal;
  font-weight: normal;
  font-size: 32px;
  line-height: 44px;
  text-transform: uppercase;
  letter-spacing: 0.04em;
}

.gallery__title-wrapper a {
  display: flex;
  align-items: center;

  font-family: "Arial", "Verdana", sans-serif;
  font-style: normal;
  font-weight: normal;
  font-size: 32px;
  line-height: 44px;
  text-transform: uppercase;
  letter-spacing: 0.04em;

  color: #21201f;
}

.gallery__icon {
  width: 12px;
  height: 24px;
  margin-left: 5px;
}

.gallery__big-photo {
  display: flex;
}

.gallery__small-photos {
  display: flex;
}

.gallery__hashtag {
  font-family: "Arial", "Verdana", sans-serif;
  font-style: normal;
  font-weight: normal;
  font-size: 32px;
  line-height: 44px;
  text-transform: uppercase;
  letter-spacing: 0.04em;

  color: #21201f;
}

@media (max-width: $tablet-width) {
  .gallery__wrapper {
    padding-top: 30px;
    padding-bottom: 30px;
  }

  .gallery__inner {
    flex-direction: column;
    align-items: center;
  }

  .gallery__title-wrapper h2 {
    font-size: 26px;
    line-height: 38px;
  }

  .gallery__title-wrapper a {
    font-size: 26px;
    line-height: 38px;
  }

  .gallery__hashtag {
    font-size: 26px;
    line-height: 38px;
  }

  .gallery__big-photo img {
    width: 768px;
  }

  .gallery__small-photos img {
    width: 384px;
  }
}

@media (max-width: $mobile-width) {
  .gallery__wrapper {
    padding-top: 20px;
    padding-bottom: 20px;
  }

  .gallery__title-wrapper {
    margin-bottom: 15px;
  }

  .gallery__title-wrapper h2 {
    margin-right: 25px;

    font-size: 20px;
    line-height: 32px;
  }

  .gallery__title-wrapper a {
    font-size: 20px;
    line-height: 32px;
  }

  .gallery__hashtag {
    font-size: 20px;
    line-height: 32px;
  }

  .gallery__icon {
    width: 10px;
    height: 20px;
  }

  .gallery__inner {
    margin-bottom: 15px;
  }
}
