.banner__wrapper {
  padding-top: 60px;
  padding-bottom: 60px;
  padding-left: 4%;
  padding-right: 4%;
}

.banner__inner {
  padding-top: 23%;
  padding-left: 55%;
  padding-bottom: 6%;
  padding-right: 5%;

  background-image: url("../img/banner-rock-desktop@1x.jpg");

  background-position: 0 0;
  background-size: 100% auto;
  background-repeat: no-repeat;
}

.banner__inner h3 {
  margin: 0;
  margin-bottom: 10px;

  font-family: $syncopate-font;
  font-style: normal;
  font-weight: 700;
  font-size: 40px;
  line-height: 40px;
  text-transform: uppercase;

  color: #ffffff;
}

.banner__inner p {
  margin: 0;
  margin-bottom: 15px;

  font-family: "Arial", "Verdana", sans-serif;
  font-style: normal;
  font-weight: normal;
  font-size: 20px;
  line-height: 22px;

  letter-spacing: 0.1em;
  color: #ffffff;
}

.banner__link {
  display: flex;
  justify-content: center;
  align-items: center;
  max-width: 385px;
  padding-top: 20px;
  padding-bottom: 20px;
  padding-left: 40px;
  padding-right: 40px;

  font-family: "Arial", "Verdana", sans-serif;
  font-style: normal;
  font-weight: normal;
  font-size: 22px;
  line-height: 22px;
  text-align: center;
  text-transform: uppercase;
  color: #ffffff;

  border: 2px solid #ffffff;
}


@media (max-width: $tablet-width) {
  .banner__wrapper {
    padding-top: 30px;
    padding-bottom: 30px;
  }

  .banner__inner {
    padding-top: 25%;
    padding-left: 10%;
    padding-bottom: 5%;
    padding-right: 5%;

    background-image: url("../img/banner-rock-tablet@1x.jpg");
  }

  .banner__inner h3 {
    font-size: 50px;
    line-height: 50px;
  }

  .banner__inner p {
    font-size: 24px;
    line-height: 30px;
  }

  .banner__link {
    padding-top: 25px;
    padding-bottom: 25px;

    font-size: 28px;
    line-height: 28px;
  }
}

@media (max-width: $mobile-width) {
  .banner__wrapper {
    padding-top: 20px;
    padding-bottom: 20px;
    padding-left: 0;
    padding-right: 0;
  }

  .banner__inner {
    padding: 0;
    padding-top: 100%;
  }

  .banner__inner h3 {
    margin-left: auto;
    margin-right: auto;
    max-width: 320px;

    font-size: 36px;
    line-height: 42px;
    color: #21201f;
    text-align: center;
  }

  .banner__inner p {
    font-size: 16px;
    color: #21201f;
    text-align: center;
  }

  .banner__link {
    max-width: 300px;
    padding-top: 15px;
    padding-bottom: 15px;
    margin-left: auto;
    margin-right: auto;

    font-size: 18px;
    line-height: 18px;
    color: #21201f;
    text-align: center;

    border: 2px solid #21201f;
  }
}
