.page-footer__wrapper {
  padding-top: 60px;
  padding-bottom: 60px;
}

.page-footer__subscribe {
  max-width: 50%;
  margin-left: auto;
  margin-right: auto;
  margin-bottom: 60px;
}

.page-footer__nav {
  display: flex;
  justify-content: flex-start;
  padding-right: 5%;
}

.page-footer__inner {
  display: flex;
  flex-direction: column;
  flex-basis: 25%;
}

.page-footer__inner h3 {
  margin: 0;
  margin-bottom: 40px;

  font-family: "Arial", "Verdana", sans-serif;
  font-style: normal;
  font-weight: normal;
  font-size: 20px;
  line-height: 32px;

  letter-spacing: 0.04em;
  text-transform: uppercase;

  color: #21201f;
}

.page-footer__list {
  @include reset-list;
}

.page-footer__item:not(:last-child) {
  margin-bottom: 25px;
}

.page-footer__link {
  display: block;

  font-family: "Arial", "Verdana", sans-serif;
  font-style: normal;
  font-weight: normal;
  font-size: 14px;
  line-height: 18px;

  text-transform: uppercase;

  color: #21201f;

  &--email {
    text-transform: none;
  }
}

.page-footer__icon-wrapper {
  display: flex;
}

.page-footer__icon {
  width: 18px;
  height: 18px;
}

.page-footer__social-text {
  padding-left: 10px;
}

@media (max-width: $tablet-width) {
  .page-footer__wrapper {
    padding-top: 30px;
    padding-bottom: 30px;
  }

  .page-footer__nav {
    padding-right: 0;
  }

  .page-footer__subscribe {
    max-width: 65%;
  }

  .page-footer__inner h3 {
    margin-bottom: 20px;
  }
}

@media (max-width: $mobile-width) {
  .page-footer__wrapper {
    padding-top: 20px;
    padding-bottom: 20px;
  }

  .page-footer__nav {
    flex-direction: column;
  }

  .page-footer__subscribe {
    max-width: 100%;
    margin-bottom: 40px;
  }

  .page-footer__inner h3 {
    margin-bottom: 15px;
  }

  .page-footer__inner:not(:last-child) {
    margin-bottom: 25px;
  }

  .page-footer__item:not(:last-child) {
    margin-bottom: 15px;
  }
}
