.featured-products h2 {
  margin: 0;
  margin-bottom: 30px;

  font-family: "Arial", "Verdana", sans-serif;
  font-style: normal;
  font-weight: normal;
  font-size: 32px;
  line-height: 48px;
  text-transform: uppercase;
  letter-spacing: 0.04em;
}

.featured-products__wrapper {
  padding-top: 60px;
  padding-bottom: 60px;
}

.featured-products__list {
  @include reset-list;

  display: flex;
  justify-content: center;
}

.featured-products__item {
  display: flex;
  flex-direction: column;
  justify-content: center;
  margin-left: 10px;
  margin-right: 10px;
}

.featured-products__link {
  margin-bottom: 25px;
}

.featured-products__link img {
  display: block;
}

.featured-products__description h3 {
  margin: 0;
  margin-bottom: 35px;

  font-style: normal;
  font-weight: 400;
  font-size: 18px;

  text-transform: uppercase;
}

.featured-products__button {
  text-decoration: underline;

  color: #21201f;
}

@media (max-width: $tablet-width) {
  .featured-products__wrapper {
    padding-top: 30px;
    padding-bottom: 30px;
  }

  .featured-products h2 {
    margin-bottom: 0;

    font-size: 26px;
    line-height: 32px;
  }

  .featured-products__list {
    flex-wrap: wrap;
  }

  .featured-products__item {
    margin-top: 20px;
    margin-bottom: 20px;
  }
}

@media (max-width: $mobile-width) {
  .featured-products__wrapper {
    padding-top: 20px;
    padding-bottom: 20px;
  }

  .featured-products__item {
    margin: 0;
    flex-basis: 100%;
    align-items: center;
  }

  .featured-products__link {
    margin-bottom: 0;
  }

  .featured-products h2 {
    margin-bottom: 15px;

    font-size: 20px;
    line-height: 32px;
  }

  .featured-products__description {
    display: none;
  }
}
